import { Button } from "@/components/ui/button"
import { Play } from "lucide-react"

export default function FreeTrialSection() {
  return (
    <section className="w-full max-w-6xl mx-auto p-8 bg-[#f8f3ee] rounded-xl">
      <div className="flex flex-col items-center justify-center text-center max-w-2xl mx-auto space-y-6">
        <h2 className="text-4xl md:text-5xl font-bold tracking-tight">Start your 30-day free trial.</h2>
        <p className="text-lg text-muted-foreground">Join over 2,500+ startups already growing with Lexend.</p>

        <div className="w-full max-w-md space-y-4">
          <Button variant="outline" className="w-full py-6 bg-white hover:bg-white/90">
            <Play className="mr-2 h-4 w-4" />
            View demo
          </Button>

          <Button className="w-full py-6 bg-[#0e6e52] hover:bg-[#0e6e52]/90">Start free trial</Button>
        </div>
      </div>
    </section>
  )
}